@import url(https://fonts.googleapis.com/css?family=Maven+Pro:700);
@import url(https://fonts.googleapis.com/css?family=PT+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.btn {
  border: 1px solid #2cbadb;
  font-size: .8rem;
  color: #2cbadb;
  padding: 8px 12px;
  cursor: pointer;
  -webkit-transform: translateY(-24%);
          transform: translateY(-24%);
  font-family: 'PT Mono', monospace;
  transition: .2s all linear;
  letter-spacing: 1.5px;
  border-radius: 2px;
  font-size: 1rem;
  background-color: transparent;
}

.btn:hover {
  background-color: rgba(44, 186, 219, .1);
}

.btn__project {
  margin-bottom: 20px;
  margin: 10px;
  border: 2px solid #2cbadb;
  padding: 9px 14px;
  font-size: 1rem;
}

.btn__home {
  margin-bottom: 100px;
  border: 2px solid #2cbadb;
  padding: 9px 14px;
  font-size: 1.2rem;
  font-weight: 700;
}

.btn__resume {
  color: #2cbadb;
}

.btn__resume--mobile {
  margin-top: 15px;
  font-size: 1.2rem;
}

@media only screen and (max-width: 1000px) {
  .btn__home {
    border: 2px solid #2cbadb;
    padding: 12px 17px;
  }
}
.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
  margin-right: 20px;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button__line {
  width: 30px;
  height: 2px;
  background: #ffffff;
}

@media only screen and (max-width: 500px) {
  .toggle-button {
    margin-right: 5px;
  }
}

nav {
  background-color: rgb(3, 31, 37);
}

.nav__primary {
  display: flex;
  height: 90px;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  max-width: 1000px;
  font-family: 'PT Mono', monospace;
  overflow: hidden;
}

.nav-right {
  float: right;
  margin-right: 15px;
}

.nav__desktop-links {
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
}

img {
  width: 100px;
  vertical-align: middle;
  /* top: 50%; */
}

.nav-link__mobile {
  font-size: 1.2rem;
}
ul {
  display: flex;
}

li {
  font-size: 1rem;
  list-style: none;
  margin-left: 30px;
  letter-spacing: 1.5px;
}

a {
  text-decoration: none;
  color: #ffffff;
  transition: all .2s;
}

li:not(:last-child) a:hover {
  color: #2cbadb;
  border-bottom: 1px solid #2cbadb;
  padding-bottom: 6px;
}

@media only screen and (max-width: 600px) {
  .menu--desktop {
    display: none;
  }

  .nav__primary {
    height: 80px;
    padding-left: 15px;
    padding-right: 5px;
  }
}

@media only screen and (min-width: 601px) {
  .menu--mobile {
      display: none;
  }
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: rgb(3, 31, 37);
  color: #2cbadb;
  font-size: .8rem;
  text-align: center;
}

.footer__text {
  padding: 5px;
}
.large-hero {
  position: relative;
}

.large-hero__text-content {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
    left: 0;
    width: 100%;
}

p {
  font-family: 'PT Mono', monospace;
}

.primary__intro {
  margin-bottom: 0;
  font-size: 1.2rem;
  letter-spacing: .2px;
  color: #2cbadb;
}

.primary__name {
  display: inline-block;
  font-size: 4rem;
  margin: 0;
  margin-top: 4px;
  font-family: 'Maven Pro', sans-serif;
  background-color: #2cbadb;
  padding: 6px;
  border-radius: 2px;
}

.primary__description {
  font-family: 'Maven Pro', sans-serif;
  background-color: rgb(3, 31, 37);
  color: #2cbadb;
  font-size: 2.6rem;
  margin-top: 0;
  margin-bottom: 28px;
  display: inline-block;
  padding: 8px;
  border-radius: 2px;
}

.btn--dark {
  margin-bottom: 80px;
}

.primary__detail {
  width: 60%;
  margin-bottom: 48px;
  line-height: 1.4;
}

@media only screen and (max-width: 500px) {
  .large-hero__text-content {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }
  .primary__intro {
    font-size: 1.4rem;
  }
  .primary__name {
    line-height: .9;
  }
  .primary__button--mobile {
    border: 2px solid red;
    color: red;
    font-size: 1.2rem;
  }

}

@media all and (orientation:landscape) {
  .large-hero__text-content {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }
}

@media only screen and (min-height: 1000px) {
  .large-hero__text-content {
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
  }
}
.project-item {
  display: block;
  background-color: rgb(3, 31, 37);
  padding: 20px;
  border-radius: 4px;
  width: 90%;
  margin: 0 auto;
  margin-bottom: 40px;
  padding-left: 40px;
  padding-right: 40px;
}

.project-item__title {
  display: block;
  font-weight: 300;
  color: #2cbadb;
  padding: 6px;
  border-bottom: 1px solid #2cbadb;
  margin: 0 auto;
  margin-bottom: 35px;
  text-align: center;
  width: 40%;
}

.project-item__display {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.project-item_button-display {
  display: flex;
  flex-direction: row;
  margin-left: 30px;
}

.project-item__button {
  display: block;
  margin-bottom: 10px;
}

.project-item__img {
  display: block;
  margin-bottom: 30px;
  text-align: center;
  width: 400px;
  height: auto;
  border: 1px solid black;
  border-radius: 2px;
  background-color: #2cbadb;
}

.project-item__description {
  color: #ffffff;
  margin-top: 20px;
  line-height: 1.4;
  border-top: 1px solid #ffffff;
  padding-top: 8px;
  padding-left: 10px;
  padding-right: 10px;
}

@media only screen and (max-width: 625px) {
  .project-item {
    padding-left: 20px;
    padding-right: 20px;
  }
  
  .project-item_button-display {
    margin-left: 10px;
  }
}

@media only screen and (max-width: 590px) {
  .project-item__display {
    flex-direction: column;
  }

  .project-item__title {
    margin-bottom: 25px;
    width: 60%;
  }

  .project-item_button-display {
    margin-top: 20px;
    flex-direction: row;
    justify-content: space-between;
  }

  .project-item__img {
    margin-right: 0;
    margin-bottom: 10px;
    width: 90%;
  }

  .project-item__description {
    margin-top: 0;
  }

  .tag {
    display: block;
  }
}

.tag {
  color: rgba(44, 186, 219, .6);
  text-align: center;
  font-weight: 300;
  margin-bottom: 5px;
}
.about__container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  background-color: rgb(3, 31, 37);
  padding: 20px;
  border-radius: 4px;
  width: 70%;
  margin: 0 auto;
  margin-bottom: 100px;
  padding: 40px;
  color: #2cbadb;
  font-family: 'PT Mono', monospace;
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.4;
  letter-spacing: 1.2px;
}

.about__description {
  max-width: 350px;
}

.img__about {
  background-color: #2cbadb;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
  width: 50px;
  transition: 0.3s ease-out;
}

.img__about:hover,
.img__about:active {
  background-color: #ffffff;
}

.about__links {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.terminal {
  display: inline;
  overflow: hidden; 
  border-right: 2px solid #2cbadb; 
  padding-right: 6px;
  -webkit-animation: blink .75s step-end infinite;
          animation: blink .75s step-end infinite;
}

/* The typewriter cursor effect */
@-webkit-keyframes blink {
  from, to { border-color: transparent }
  50% { border-color: #2cbadb; }
}
@keyframes blink {
  from, to { border-color: transparent }
  50% { border-color: #2cbadb; }
}

@media only screen and (max-width: 800px) {
  .about__container {
    flex-direction: column;
    width: 80%;
    padding: 20px;
  }

  .about__description {
    margin: 0 auto;
  }

  .about__links {
    flex-direction: row;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .link__email {
    margin-top: 4px;
  }
}
.react-pdf__Page__canvas {
  width: 100% !important;
  max-width: 800px;
  height: auto !important;
  margin: 0 auto;
}

.pageTwo {
  margin-top: -80px;
}
.side-drawer {
  height: 100%;
  background: #ffffff;
  position: fixed;
  top: 0;
  right: 0;
  width: 60%;
  max-width: 400px;
  z-index: 150;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  background-color: rgb(3, 31, 37);
}

.side-drawer.open {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.5);
}

.image__logo--sidebar {
  width: 200px;
}

.side-drawer ul {
  height: 100%;
  margin-top: 40%;
  list-style: none;
  flex-direction: column;
  justify-content: center;
  font-size: 1.5rem;
  align-items: center;
  padding: 0;
  font-family: 'PT Mono', monospace;
}

.side-drawer li {
  margin: 0.5rem 0;
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 2px solid #2cbadb;
}

.side-drawer__border--none {
  border-bottom: none !important;
}

.side-drawer a {
  color: #2cbadb;
  text-decoration: none;
}

.side-drawer a:hover, 
.side-drawer a:active {
  color: #2cbadb;
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background: rgba(0,0,0,0.3);
  z-index: 100;
}
.wrapper {
  padding-left: 18px;
  padding-right: 18px;
  padding-bottom: 50px;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 500px) {
  .wrapper {
    padding-left: 8px;
    padding-right: 8px;
  }
}
